.tabulation {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.nav-item {
    position: relative;
}
.closeTab {
    position: absolute;
    right: 7px;
    top: 9px;
}

.closeTab > i,
.closeTab > span {
    color: lightgrey;
}

.closeTab > i:hover,
.closeTab > span:hover {
    color: red;
    cursor: pointer;
}

.tab-content,
.tab-pane {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}



